.highlight__text--bordered{border-bottom:3px solid #51A795;padding:3px 10px
}
.carousel{display:block;position:relative;width:100%
}
.carousel__button,.carousel__text{position:absolute
}
.carousel__button{bottom:0;margin:auto;top:0;z-index:1001
}
.carousel__button--next{right:-20px
}
.carousel__button--prev{left:-20px
}
.carousel__text{border-bottom:2px dashed transparent;bottom:10px;color:#fff;left:0;margin:0 10px;padding:0 0 5px;right:0;text-align:center;width:calc(100% - 20px);z-index:1002
}
.carousel__text::before{bottom:-100%;box-shadow:0 -25px 40px 10px rgba(0,0,0,0.6) inset;content:'';height:220px;left:-80%;position:absolute;transition:all 0.6s cubic-bezier(0.94, 0.06, 0.05, 0.95);width:200%;z-index:1000
}
.carousel__block{border-bottom:6px solid #65C4A9;min-height:200px;overflow:hidden
}
@media (max-width: 767px){
.carousel__block{max-height:200px
}
}
.carousel__block::after,.carousel__block::before,.carousel__block *{transition:all 0.6s cubic-bezier(0.94, 0.06, 0.05, 0.95)
}
.carousel__block::after,.carousel__block::before{bottom:0;left:0;opacity:0;position:absolute;visibility:hidden
}
.carousel__block::after{background-color:rgba(101,196,169,0.5);content:'';height:100%;width:100%;z-index:999
}
.carousel__block::before{-ms-flex-align:center;align-items:center;background-color:#fff;border-radius:50%;color:#000;content:"\f04b";display:-ms-flexbox;display:flex;font:normal normal normal 20px/1 FontAwesome;height:56px;-ms-flex-pack:center;justify-content:center;margin:auto;right:0;text-align:center;top:0;width:56px;z-index:1003
}
.carousel__block:hover::after,.carousel__block:hover::before{opacity:1;visibility:visible
}
.carousel__block:hover .carousel__text{border-bottom-color:#fff;font-weight:bold;transform:translateY(-50%)
}
.bar{background-color:#65C4A9;height:55px;margin-top:20px;-ms-flex-pack:justify;justify-content:space-between
}
@media (max-width: 480px){
.bar{-ms-flex-direction:column;flex-direction:column;height:auto
}
}
.bar__highlight,.bar__icon{-ms-flex-align:center;align-items:center;display:-ms-flexbox;display:flex
}
.bar__highlight{-ms-flex-positive:1;flex-grow:1;-ms-flex-pack:start;justify-content:flex-start;padding:0 15px
}
.bar__highlight>*{margin:0 5px
}
.bar__icon{color:#fff;-ms-flex-pack:center;justify-content:center;text-align:center;width:80px
}
@media (max-width: 480px){
.bar__icon{width:100%
}
}
.bar__icon--yellow{background-color:#F2CE3E
}
.bar__icon--black{background-color:#000
}
.bar__icon--green{background-color:#58C63C
}
.bar__icon--turquoise{background-color:#65D78C
}
.is-hidden{display:none !important
}
.is-shown{display:block !important
}
.is-invisible{opacity:0;visibility:hidden
}
.is-visible{opacity:1;visibility:visible
}
.is-unguttered{padding:0 !important
}
.is-visible{opacity:1
}
.is-invisible{opacity:0
}
img{animation:fadeIn
}
h1{text-transform:capitalize
}
@keyframes fadeIn{
0%{opacity:0
}
100%{opacity:1
}
}
.carousel__holder.box.box--row img{max-width:200px;max-height:200px
}
.hide{display:none
}
@media (max-width: 480px){
.show--mobile{display:-ms-flexbox !important;display:flex !important
}
.hide--mobile{display:none !important
}
}
@media (max-width: 767px){
.show--mobile{display:-ms-flexbox !important;display:flex !important
}
.hide--mobile{display:none !important
}
}
.fade-enter-active,.fade-leave-active{transition:opacity .5s
}
.fade-enter,.fade-leave-to{opacity:0
}
.move-enter-active,.move-leave-active{transform:translateX(0);transition:all .3s linear
}
.move-enter,.move-leave-to{transform:translateX(100%)
}
.moveup-enter-active,.moveup-leave-active{transform:translateY(0);transition:all .5s linear
}
.moveup-enter,.moveup-leave-to{transform:translateY(100%)
}



